import styled from "styled-components";
import NextSeo from "next-seo";

import Layout from "../components/Layout";
import LetterToSanta from "../components/LetterToSanta";

const ContactDiv = styled.div`
  height: 85vh;

  h1 {
    font-family: "Cookie", cursive;
    font-size: 4.5vw;
    text-align: center;
  }

  #innerBorder {
    width: 100%;
    height: 100%;
    border: 0.2vw solid black;
  }

  #satmPostCard {
    margin: 0;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0%);
    width: 40vw;
    height: 25vw;
    border: 1vw dashed rgba(192, 8, 8, 1);
    outline: 0.2vw solid black;
    box-shadow: -0.3vw 0.3vw 1vw rgba(0, 0, 0, 0.8);
  }
`;

const Contact = () => (
  <Layout>
    <ContactDiv>
      <NextSeo
        config={{
          title: "Santa & the Mrs of West Texas",
          description: "Santa and Mrs Claus services for West Texas"
        }}
      />
      <LetterToSanta />
    </ContactDiv>
  </Layout>
);

export default Contact;
